html {
  background: radial-gradient(at top left, purple, blue);
  background-size: cover;
  background-attachment: fixed;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
}

@font-face {
  font-family: "carbon";
  src: url("./CarbonBlock.ttf") format("truetype");
  font-weight: 600;
  font-style: normal;
  size-adjust: 130%;
}

#root {
  min-height: 100%;
}

h1 {
  font-family: "carbon";
}

h2 {
  font-family: "carbon";
}

h3 {
  font-family: "carbon";
}

h4 {
  font-family: "carbon";
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header {
  background: black;
  color: aquamarine;
  display: flex;
  align-items: center;
  width: 100%;
  padding-left: 5em;
  
}

@media ( max-width: 600px ) {
  header {
    padding-left: none;
  }
}


.sub-head {
  display: flex;
  justify-content: center;
  align-items: center;
  background: black;
  color: white;
}

.nav-tab {
  border-right: 1px solid rgb(0, 255, 255, .3);
  border-left: 1px solid rgb(0, 255, 255, .3);
  padding: 1em;
  margin-right: 1em;
  margin-left: 1em;
  transform: none;
  transition: transform .2s ease-in-out;
  cursor: pointer;
}

.nav-tab:hover {
  background: linear-gradient(to top, rgb(0, 255, 255, .5), black);
  transform: scale(107%);
}

li {
  margin-left: 3em;
}

.icon {
  height: 1.5em;
  width: 1.5em;
  background: url("./menu.svg");
  background-size: cover;
  margin-right: 5em;
  cursor: pointer;
  border-right: 1px solid rgb(0, 0, 0, .3);
  border-left: 1px solid rgb(0, 0, 0, .3);
  transform: none;
  transition: transform .2s ease-in-out;
}

a {
  text-decoration: none;
  color: white;
}


.icon:hover {
  border-right: 1px solid rgb(0, 255, 255, .3);
  border-left: 1px solid rgb(0, 255, 255, .3);
  transform: scale(107%);
}

main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.info-box {
  color: white;
  background: rgb(0, 0, 0, .3);
  width: 20em;
  margin: 1em;
  padding: 1em;
}

.App {
  min-height: 100vh;
}

.home-logo {
  margin-top: 2em;
  height: 30em;
  width: 30em;
  border-top: 4px solid black;
  border-bottom: 4px solid black;
  padding-top: 1.5em;
}

@keyframes stripes {
  from { transform: translateY(-50%); }
  to {transform: translateY(50%); }
}

.home-stripe {
  min-width: 100%;
  min-height: 10%;
  border-top: 1px solid aqua;
  animation-name: stripes;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-delay: none;
  animation-timing-function: linear;
}

.diamond {
  /* position: absolute; */
  transform: translateY(-100%);
  width: 30em;
  height: 30em;
  background: url("./diamond.png");
  background-size: cover;
}

